.footer {
    width: 100%;
    background-color: #1D2530;
    // .footer__container

    &__container {
        width: 100%;
        height: 100%;
    }

    // .footer__body

    &__body {
        padding: 110px 0 70px 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        font-family: "Gilroy", sans-serif;
        max-width: 930px;
    }

    // .footer__logo

    &__logo {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-decoration: none;
    }

    // .footer__image

    &__image {
        width: 81px;
        height: 59px;
    }

    // .footer__text

    &__text {
        color: #fff;
        font-size: 32px;
        font-weight: 700;
    }

    // .footer__content

    &__content {
        display: flex;
        @media (max-width: $md-5) {
            flex-direction: column;
        }
    }

    // .footer__column

    &__column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin: 0 60px; 
        @media (max-width: $md-4) {
            margin: 0 18px; 
        }
        @media (max-width: $md-5) {
            margin: 0 0 24px 0;
        }
    }

    // .footer__caption

    &__caption {
        color: #fff;
        font-weight: 500;
        font-size: 20px;
        margin: 0 0 32px 0;
        @media (max-width: $md-5) {
            margin: 0 0 16px 0;
        }
    }

    // .footer__items

    &__items {
        display: flex;
        flex-direction: column;
        padding: 0;
    }

    // .footer__item

    &__item {
        list-style: none;
        margin: 6px 0;
        max-width: 190px;
        a {
            color: #EEEFF4;
            text-decoration: none;
            font-weight: 400;
            font-size: 16px;
        }
    }
}
