.header {
    position: absolute;
    width: 100%; 
    height: 76px;
    top: 0;
    left: 0;
    z-index: 50;
    display: flex;
    align-items: flex-end;
    @media (max-width: 768px) {
        height: 56px;
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        z-index: 2;
    }
    // .header__container

    &__container {
        width: 100%;
        height: 100%;
    }

    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        position: relative;
        
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-family: "Gilroy";
    }

    // .header__logo

    &__logo {
       display: flex;
       align-items: center;
       cursor: pointer;
       text-decoration: none;
       position: relative;
       z-index: 5;
    }

    // .header__ico

    &__image {
      
    }

    // .header__text

    &__text {
        color: $blackColor;
        font-weight: 700;
        margin: 0 0 0 2px;
        font-size: 28px;
        text-decoration: none;
    }

    // .header__burger
    &__burger.active {
        span {
            display: none;
        }
        &::before {
            transform: rotate(-45deg);
            top: 9px;
        }
        &::after {
            transform: rotate(45deg);
            bottom: 7px;
        }
    }
    &__burger {
        display: none;
        z-index: 10;
        @media (max-width: $md-4) {
            
           display: block;
            position: relative;
            width: 30px;
            height: 18px; 
            cursor: pointer;
            
            &::before,
            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 2px;
                left: 0;
                transition: all .3s ease 0s;
            }
            &::before {
                top: 0;
                background-color: $mainColor;
            }
            &::after {
                bottom: 0;
                background-color: $hoverColor;
            }
            span {
                position: absolute;
                width: 100%;
                height: 2px;
                background-color: $activeColor;
                left: 0;
                top: 8px;
            }  
        }
        
    }

    // .header__menu

    &__menu {
       
    }
   
}

.menu.active {
    left: 0;
}
.menu {
    transition: all .3s ease 0s;
    z-index: 5;
    @media (max-width: $md-4) {
        position: fixed;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background-color: #fff;
        z-index: 1;
        transition: .3s;
        padding: 70px 15px 0 15px;
    }
    // .menu__items

    &__items {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        @media (max-width: 768px) {
            flex-direction: column;
            align-items: center;
            padding: 0;
        }
    }

    // .menu__item

    &__item {
        list-style: none;
        margin: 0 30px;
        @media (max-width: $md-3) {
            margin: 0 20px;
        }
        @media (max-width: $md-4) {
            margin: 15px;
        }
        a {
            
            font-weight: 700;
            font-size: 20px;
            cursor: pointer;
            color: $blackColor;
            transition: .3s;
            text-decoration: none;
            @media (max-width: $md-4) {
                font-size: 25px;
            }
            &:hover {
                transition: .3s;
                color: $mainColor;
            }
        }
    }
}

.menu__item_active {
    a {
        color: $mainColor;
    }
}