.copyright {
    width: 100%;
    height: 72px;
    background-color: #141B23;
    @media (max-width: $md-4) {
       height: 50px;
    }
    // .copyright__container

    &__container {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        @media (max-width: $md-4) {
            justify-content: center;
        }
    }

    // .copyright__text

    &__text {
        font-family: "Gilroy", serif;
        font-weight: 400;
        font-size: 14px;
        color: #D9DBE1;
    }
}
