.review {
    width: 100%;
    height: 100%;
    padding: 17px 0 122px 0;
    @media (max-width: $md-4) {
        padding: 60px 0 86px 0;
    }
    // .review__container

    &__container {
        position: relative;
    }

    // .review__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-family: "Gilroy", sans-serif;
        color: $blackColor;
    }

    // .review__title

    &__title {
        font-weight: 800;
        font-size: 46px;
        margin: 0 0 48px 0;
        @media (max-width: $md-4) {
            margin: 0 0 40px 0 ;
            font-size: 32px;
        }
    } 

    // .review__image

    &__image {
        position: absolute;
        top: -40px;
        right: 0;
        z-index: -50;
        @media (max-width: $md-4) {
            width: 112px;
            right: 15px;
        }
        @media (max-width: $md-5) {
            width: 54px;
            top: 0;
        }
        @media (max-width: 420px) {
            width: 54px;
            top: -40px;
        }
    }
}
 
.review-slider {
    width: 100%;
    // .review-slider__item

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 15px;
        @media (max-width: $md-4 + 1px) {
            margin: 0 80px 0 0 ;
        }
        @media (max-width: $md-5) {
            margin: 0 15px;
        }
    }

    // .review-slider__comment

    &__comment {
        max-width: 540px;
        min-height: 220px;
        padding: 32px 68px;
        background-color: #fff;
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
        position: relative;
        margin: 0 0 16px 0;
        border-radius: 6px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        &::before {
            content: "";
            position: absolute;
            bottom: -6px;
            left: 50%;
            width: 15px;
            height: 15px;
            background: #fff;
            transform: rotate(45deg);
        }
        @media (max-width: 1000px) {
            height: 260px;
        }
        @media (max-width: $md-4) {
            height: 225px;
            padding: 32px;
        }
        @media (max-width: 570px) {
            height: 265px;
        }
        @media (max-width: 515px) {
            height: 300px;
        }
        @media (max-width: $md-5) {
            height: 220px;
        }
        @media (max-width: 380px) {
            height: 250px;
        }
    }

    // .review-slider__caption

    &__caption {
        font-size: 18px;
        font-weight: 700;
        margin: 0 0 8px 0;
    }

    // .review-slider__text

    &__text {
        font-size: 14px;
        font-weight: 400;
    }

    // .review-slider__user

    &__user {
        display: flex;
        align-items: center;
    }

    // .review-slider__userimage

    &__userimage {
        width: 56px;
        height: 56px;
        border-radius: 50%;
        margin: 0 16px 0 0 ;
    }

    // .review-slider__info

    &__info {
        
    }

    // .review-slider__name

    &__name {
        font-weight: 700;
        font-size: 18px;
    }

    // .review-slider__email

    &__email {
        font-size: 14px;
        font-weight: 400; 
    }
}


.review-slider .slick-dots{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 30px 0 0 0;
    @media (max-width: $md-5) {
        margin: 16px 0 0 0;
    }
    @media (max-width: 380px) {
        margin: 16px 20px 0 0;
    }
}
.review-slider .slick-dots li{
    list-style: none;
}
.review-slider .slick-dots li button{
    font-size: 0;
    width: 10px;
    height: 15px;
    background-color: #fff;
    border-radius: 50%;
    border: 2px solid rgba(104, 94, 214, .6);
    margin: 0 5px;
    cursor: pointer;
}
.review-slider .slick-dots li.slick-active button{
    background-color: $mainColor;
}
