.steps {
    width: 100%;
    padding: 0 0 93px 0;
    @media (max-width: $md-2) {
        padding: 0;
    }
    @media (max-width: $md-3) {
        padding: 0 0 60px 0;
    }
    @media (max-width: $md-5) {
        padding: 0;
    }
    // .steps__container
 
    &__container {
        width: 100%;
        height: 100%;
        padding: 0 40px;
    }

    // .steps__body

    &__body {
        width: 100%;
        height: 100%;
        
        display: flex;
        flex-direction: column;
        align-items: center;
        font-family: "Gilroy", sans-serif;
        color: $blackColor;
    }

    // .steps__title

    &__title {
        font-weight: 800;
        font-size: 48px;
        margin: 0 0 40px 0;
        text-align: center;
        @media (max-width: $md-2) {
            font-size: 32px;
        }
    } 

    // .steps__content

    &__content {
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: relative;
        @media (max-width: $md-3) {
           align-items: center;
        }
        @media (max-width: $md-4 - 1px) {
            flex-direction: column;
        }
    }
    // .steps__item

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        @media (max-width: $md-3) {
            margin: 0 15px;
        }
        &_center {
            margin: 70px 0 0 0 ;
            @media (max-width: $md-1) {
                margin: 0;
            }
        }
    }

    // .steps__number

    &__number {
        font-size: 30px;
        font-weight: 700;
        color: $mainColor;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 72px;
        height: 72px;
        border-radius: 50%;
        margin: 0 0 16px 0;
        background-color: #F1E4FF;
    }

    // .steps__caption

    &__caption {
        font-weight: 700;
        font-size: 18px;
        margin: 0 0 4px 0;
        max-width: 214px;
    }

    // .steps__text

    &__text {
        font-weight: 400;
        font-size: 16px;
        margin: 0 0 40px 0 ;
        max-width: 214px;
        @media (max-width: $md-3) {
            max-width: 209px;
            height: 76px;

        }
    }

    // .steps__image

    &__image { 
        @media (max-width: $md-3) {
            width: 211px;
            height: 422px;
        }
        @media (max-width: $md-4 - 1px) {
            display: none;
        }
    }

    &__image-container {
        position: relative;
    }

    &__shadow {
        width: 90px;
        height: 90px;
        background-color: red;
        position: absolute;
        z-index: -1;
        @media (max-width: $md-2) {
            width: 50px;
            height: 50px;
        }
        @media (max-width: $md-4 - 1px) {
            display: none;
        }
        &_1 {
            background-color: $hoverColor;
            bottom: 30px;
            left: 15px;
            width: 100px;
            height: 140px;
            box-shadow: -45px 15px 90px 20px rgba(51, 175, 225, 0.6);
            @media (max-width: $md-2) {
                width: 50px;
                height: 50px;
                box-shadow: 0px -60px 90px 20px rgba(51, 175, 225, 0.5);
            }
        }
        &_2 {
            background-color: $mainColor;
            top: 180px;
            left: 20px;
            box-shadow: -6px 6px 80px 20px rgba(104, 94, 214, 0.6);
            @media (max-width: $md-2) {
                width: 50px;
                height: 50px;
                box-shadow: 0px 65px 80px 20px rgba(104, 94, 214, 0.5);
            }
        }
        &_3 {
            background-color: $hoverColor;
            bottom: 90px;
            right: 40px;
            height: 90px;
            box-shadow: 45px 0px 90px 20px rgba(51, 175, 225, 0.6);
            @media (max-width: $md-2) {
                bottom: 10px;
                width: 50px;
                height: 50px;
                box-shadow: 30px 0px 90px 20px rgba(51, 175, 225, 0.5);
            }
        }
        &_4 {
            background-color: $mainColor;
            bottom: 30px;
            left: 30px;
            height: 130px;
            box-shadow: -30px 10px 90px 20px rgba(104, 94, 214, 0.6);
            @media (max-width: $md-2) {
                display: none;
            }
        }
    }

    &__lines-image {
        position: absolute;
        top: -15px;
        @media (max-width: $md-1) {
            display: none;
        }
    }
}
 