.online {
    width: 100%;
    margin: 40px 0 100px 0;
    padding: 70px;
    @media (max-width: $md-3) {
        margin: 0 0 100px 0;
    }
    @media (max-width: $md-4) {
        margin: 0 0 54px 0;
    }
    @media (max-width: $md-5) {
        padding: 30px;
    }
    // .online__container
    
    &__container {
        width: 100%;
        height: 100%; 
    }
    
    // .online__body
    
    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    
    // .online__content
    
    &__content {
        display: flex;
        flex-direction: column;
        font-family: "Gilroy", sans-serif;
        max-width: 522px;
    }
    
    // .online__title
    
    &__title {
        font-weight: 800;
        font-size: 48px;
        color: $blackColor;
        margin: 0 0 68px 0;
        @media (max-width: $md-3) {
            margin: 0 0 38px 0;
            font-size: 32px;
        }
    }
    
    // .online__column
    
    &__column {
        display: flex;
        flex-direction: column;
    }
    
    // .online__item
    
    &__item {
        display: flex;
        align-items: center;
        margin: 0 0 48px 0;
        @media (max-width: $md-5) {
            margin: 0 0 16px 0;
        }
    }
    
    // .online__ico
    
    &__ico {
        width: 68px;
        height: 68px;
        margin: 0 38px 0 0;
        @include bg("contain");
        @media (max-width: $md-3) {
            width: 42px;
            height: 42px;
            margin: 0 15px 0 0 ;
            @include bg("fill");
        }
    }   

    // .online__text
    
    &__text {
        @media (max-width: 560px) {
           width: 300px;
        }
        @media (max-width: 430px) {
            width: 200px;
         }
    }

    // .online__caption
    
    &__caption {
        margin: 0 0 8px 0;
        font-weight: 600;
        font-size: 24px;
        color: $blackColor;
        @media (max-width: $md-3) {
           font-size: 20px;
        }
    }
    
    // .online__info
    
    &__info {
        font-size: 16px;
        font-weight: 400;
        color: $blackColor;
       
    }
    
    // .online__image
    
    &__image {
        margin-top: 20px;
        @media (max-width: $md-2) {
            width: 300px;
            height: 480px;
        }
        @media (max-width: $md-3) {
            display: none;
        }
        @media (max-width: $md-4) {
            display: block;
            width: 100%;
            object-fit: contain;
        }
        @media (max-width: 400px) {
           display: none;
        }
    }
}


