.tarrif {
    width: 100%;
    position: relative;
    margin: 0 0 100px 0;
    @media (max-width: $md-4) {
        margin: 0 0 54px 0;
    }
    // .tarrif__container

    &__container {
    }

    // .tarrif__body

    &__body {
        width: 100%;
        height: 100%;
        font-family: "Gilroy", sans-serif;
        color: $blackColor;
    }

    // .tarrif__top

    &__top {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 0 40px 0;
    }

    // .tarrif__title

    &__title {
        font-weight: 800;
        font-size: 46px;
        margin: 0 0 16px 0;
        @media (max-width: $md-5) {
            font-size: 32px;
        }
    }

    // .tarrif__subtitle

    &__subtitle {
        font-weight: 700;
        font-size: 24px;
        text-align: center;
        @media (max-width: $md-5) {
            font-size: 20px;
        }
    }

    // .tarrif__content

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        @media (max-width: $md-1) {
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    // .tarrif__item

    &__item {
        width: 350px;
        height: 387px;
        background-color: #fff;
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.1);
        position: relative;
        padding: 100px 30px 50px 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 6px;
        margin: 0 10px;
        @media (max-width: $md-1) {
            margin: 20px 20px;
        }
        @media (max-width: 700px) {
            width: 290px;
        }
    }

    // .tarrif__fee

    &__fee {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 61px;
        background-color: rgba(104, 94, 214, 0.05);
        color: $mainColor;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 800;
        font-size: 24px;
    }

    // .tarrif__caption

    &__caption {
        max-width: 255px;
        text-align: center;
        font-weight: 700;
        font-size: 28px;
        margin: 0 0 32px 0;
    }

    // .tarrif__price

    &__price {
        font-weight: 500;
        font-size: 20px;
        margin: 0 0 46px 0;
    }

    // .tarrif__button

    &__button {
        width: 287px;
        height: 48px;
        @media (max-width: $md-4) {
            width: 270px;
        }
    }
}
