*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars
$containerWidth: 1110px + 30px;
$md-1: $containerWidth;
$md-2: 1040px;
$md-3: 992px;
$md-4: 768px;
$md-5: 500px;
$md-6: 400px;

$mainColor: #685ED6;
$hoverColor: #33AFE1;
$activeColor: #18E1F7; 
$blackColor: #1D2530;

// Elements
.body.lock {
	overflow: hidden;
}

.container {
	max-width: $containerWidth;
	padding: 0 15px;
	margin: 0 auto;
}

.button {
	@include no-btn();
	color: #fff;
	background-color: $mainColor;
	transition: .5s;
	font-family: "Gilroy", sans-serif;
	border-radius: 5px;
	&:hover {
		transition: .5s;
		background-color: $hoverColor;
	}
	&:active {
		transition: .5s;
		background-color: $activeColor;
		cursor: pointer;
	}
}	