.send {
    width: 100%;
    min-height: 268px;
    margin: 90px 0;
    padding: 0 23px;
    
    // .send__container

    &__container {
        position: relative;
        padding: 60px 100px 0 185px;
        width: 100%;
        height: 100%;
        display: flex;
        font-family: "Gilroy", sans-serif;
        @media (max-width: $md-3) {
            padding: 60px 100px 0 20px;
        }
        @media (max-width: $md-4) {
            background-color: $mainColor;
            border-radius: 6px;
            flex-direction: column;
            align-items: center;
            padding: 40px 20px;
        }
        @media (max-width: $md-6) {
            padding: 20px 10px;
        }
    }

    // .send__image

    &__image {
        @media (max-width: $md-4) {
           width: 269px;
           height: 239px;
        }
        @media (max-width: $md-6) {
           
        }
    }

    // .send__right

    &__right {
        max-width: 445px;
        margin: 0 0 0 80px;
        @media (max-width: $md-3) {
            margin: 0 0 0 30px;
        }
        @media (max-width: $md-4) {
            margin: 24px 0 0 0;
        }
    }

    // .send__title

    &__title {
        color: #fff;
        font-weight: 800;
        font-size: 33px;
        margin: 0 0 32px 0;
        @media (max-width: $md-6) {
            font-size: 28px;
        }
    }

    // .send__form

    &__form {
        display: flex;
    }

    // .send__input

    &__input {
        outline: none;
        border: 1px solid transparent;
        font-weight: 400;
        font-size: 15px;
        padding: 14px 16px;
        border-bottom-left-radius: 4px;
        border-top-left-radius: 4px;
        @media (max-width: $md-6) {
           width: 150px;
        }
    }

    // .send__button

    &__button {
        @include no-btn();
        background-color: $blackColor;
        color: #fff;
        width: 120px;
        height: 48px;
        font-weight: 700;
        font-size: 14px;
        cursor: pointer;
        border-bottom-right-radius: 4px;
        border-top-right-radius: 4px;
        @media (max-width: $md-6) {
            width: 100px;
         }
    }

    // .send__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -50;
        object-fit: cover;
        border-radius: 10px;
        @media (max-width: $md-4) {
            display: none;
        }
    }
}
