.first-screen {
    width: 100%;
    min-height: 100vh;
    padding: 195px 0 50px 0;
    @media (max-width: $md-2) {
      min-height: 60vh;
    }
    @media (max-width: $md-3) {
        padding: 180px 0 60px 0;
    }
    @media (max-width: $md-4) {
        padding: 102px 0 0 0;
    }
    // .first-screen__container

    &__container {
        width: 100%;
        height: 100%; 
    }

    // .first-screen__body
 
    &__body {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-family: "Gilroy", sans-serif;
        position: relative;
        @media (max-width: $md-4) {
            flex-direction: column;
        }
    }

    // .first-screen__content

    &__content {
        display: flex;
        flex-direction: column;
        @media (max-width: $md-4) {
            margin: 0 0 48px 0;
        }
    }

    // .first-screen__title
    &__title, &__subtitle {
        font-weight: 800;
        font-size: 50px;
        max-width: 383px;
        @media (max-width: $md-3) {
            font-size: 32px;
        }
    }

    &__title {
        color: $mainColor;
    }

    &__subtitle {
        color: $blackColor;
        margin: 0 0 24px 0;
        
    }

    // .first-screen__text

    &__text {
        color: $blackColor;
        margin: 0 0 32px 0;
        font-weight: 400;
        font-size: 24px;
        max-width: 482px;
        span {
            color: $mainColor;
            font-weight: 800;
        }
        @media (max-width: $md-3) {
            font-size: 18px;
            max-width: 270px;
        }
    }

    // .first-screen__button

    &__button {
        width: 168px;
        height: 48px; 
        font-weight: 700;
        font-size: 18px;
        box-shadow: -80px 50px 90px 20px rgba(104, 94, 214, 0.5);
        @media (max-width: 1400) {
            box-shadow: 0px 20px 90px 20px rgba(104, 94, 214, 0.4);
        }
        @media (max-width: $md-3) {
            box-shadow: 0px 0px 90px 20px rgba(104, 94, 214, 0.3);
        }
        &:hover {
            box-shadow: -80px 50px 90px 20px rgba(51, 175, 225, 0.5);
            @media (max-width: 1400) {
                box-shadow: 0px 20px 90px 20px rgba(51, 175, 225, 0.4);
            }
            @media (max-width: $md-3) {
                box-shadow: 0px 0px 90px 20px rgba(51, 175, 225, 0.3);
            }
        }
    }

    &__shadow1, &__shadow2 {
        z-index: -51;
        width: 50px;
        height: 50px;
        position: absolute;
        @media (max-width: $md-3) {
            width: 30px;
            height: 30px;
        }
        @media (max-width: $md-4) {
            display: none;
        }
    }

    &__shadow1 {
        background-color: rgba(51, 175, 225, 0);
        top: -40px;
        right: 400px;
        box-shadow: -100px 10px 85px 20px rgba(51, 175, 225, 0.6);
        @media (max-width: $md-3) {
            right: 250px;
        }
    }
    &__shadow2 {
        top: 150px;
        right: 250px;
        background-color: red;
        box-shadow: 100px 10px 85px 20px rgba(104, 94, 214, 0.6);
        @media (max-width: $md-3) {
            right: 170px;
        }
    }

    // .first-screen__image

    &__image {
        position: absolute;
        z-index: -50;
        top: -40px;
        right: -20px;
        @media (max-width: $md-3) {
           max-width: 520px;
        }
        @media (max-width: $md-4) {
           position: relative;
           z-index: 1;
           top: 0;
           left: 0;
           width: 100%;
        }
    }
} 